.beerCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 490px;
    border-radius: 10px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background-color: white;
    position: relative;
    
    .beerCard__img {
        width: 100px;
        height: 200px;
        object-fit: contain;
    }

    .beerCard__title {
        font-size: 1.5rem; 
        text-align: center;
    }

    .beerCard__tagline {
        font-style: italic;
    }

    .beerCard__abv {
        font-weight: 600;
        background-color: #f0edee;
        border-radius: 15px;
        padding: 10px; 
    }

    .beerCard__info {
        text-align: center;
        font-size: 1rem;
        padding: 20px;
        border-radius: 10px;
        // background-color: #07393C;
        background: linear-gradient(137deg, rgba(44,102,110,1) 38%, rgba(7,57,60,1) 100%);
        line-height: 1.5rem;
        color: white;
        margin-bottom: 3rem;
        overflow: scroll;
        height: 200px;
        font-style: italic;
    }

  

    .moreInfo {
        border-radius: 10px;
        padding: 10px;
        cursor: pointer;
        width: 90%;
        text-align: center;
        background-color: #07393C;
        color: white;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

        &:hover {
            background-color: #2c666e;
        }
    }

    .moreInfoOff {
        border-radius: 10px;
        padding: 10px;
        cursor: pointer;
        width: 90%;
        text-align: center;
        background-color: #07393C;
        color: white;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        position: absolute;
        bottom: 0;
        margin-bottom: 2rem;

        &:hover {
            background-color: #2c666e;
        }
    }

    
}