@import url(https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pathway+Gothic+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Cabin",sans-serif}.app{display:flex;flex-direction:column}@media only screen and (min-width: 900px){.app{display:flex;position:relative}}
.beerCard{display:flex;flex-direction:column;align-items:center;justify-content:center;width:300px;height:490px;border-radius:10px;padding:20px;box-shadow:rgba(0,0,0,.24) 0px 3px 8px;background-color:#fff;position:relative}.beerCard .beerCard__img{width:100px;height:200px;object-fit:contain}.beerCard .beerCard__title{font-size:1.5rem;text-align:center}.beerCard .beerCard__tagline{font-style:italic}.beerCard .beerCard__abv{font-weight:600;background-color:#f0edee;border-radius:15px;padding:10px}.beerCard .beerCard__info{text-align:center;font-size:1rem;padding:20px;border-radius:10px;background:linear-gradient(137deg, #2c666e 38%, #07393c 100%);line-height:1.5rem;color:#fff;margin-bottom:3rem;overflow:scroll;height:200px;font-style:italic}.beerCard .moreInfo{border-radius:10px;padding:10px;cursor:pointer;width:90%;text-align:center;background-color:#07393c;color:#fff;box-shadow:rgba(99,99,99,.2) 0px 2px 8px 0px}.beerCard .moreInfo:hover{background-color:#2c666e}.beerCard .moreInfoOff{border-radius:10px;padding:10px;cursor:pointer;width:90%;text-align:center;background-color:#07393c;color:#fff;box-shadow:rgba(99,99,99,.2) 0px 2px 8px 0px;position:absolute;bottom:0;margin-bottom:2rem}.beerCard .moreInfoOff:hover{background-color:#2c666e}
.main{min-height:100vh;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;grid-gap:2rem;gap:2rem;background-color:#f0edee;padding:20px}@media only screen and (min-width: 900px){.main{margin-left:100px;justify-content:center;padding-left:150px}}
.navbar{display:flex;flex-direction:column;align-items:center;grid-gap:1rem;gap:1rem;color:#fff;background-color:#07393c;height:300px;padding:10px}.navbar .navbar__title{font-size:2.5rem;font-weight:900;color:#fff;text-transform:uppercase}@media only screen and (min-width: 900px){.navbar{height:100vh;position:fixed;margin-right:100px}}
.search{width:150px;margin-bottom:20px}.search .search__box{width:100%;font-size:1rem;background:transparent;border:none;border-bottom:1px solid #000;color:#fff;font-size:1rem}.search .search__box:focus{outline:none}
.filter{width:200px;display:flex;flex-direction:column;align-items:flex-start;grid-gap:1rem;gap:1rem}.filter .filter__title{font-size:1rem}.filter .filter__item{width:100%;display:flex;justify-content:space-between}
